/* General Layout */
.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f2e0c6; /* Soft parchment-like background */
  font-family: 'Arial', sans-serif;
}

/* NAVBAR */
.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #8b5e34; /* Brownish color */
  color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.tab-list {
  list-style: none;
  display: flex;
  gap: 40px;
}
.tab {
  cursor: pointer;
  font-weight: 600;
  color: #f8f8f8;
  transition: color 0.3s;
}
.tab:hover,
.tab.active {
  color: #ffd700; /* Highlight active tab with gold */
}
.gen-btn {
  padding: 10px 20px;
  background-color: #6a4e23;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: background 0.2s;
}
.gen-btn:hover {
  background-color: #8b5e34;
}

/* DISPLAY CARD */
.display-card {
  flex: 1;
  background: #f5f1e1; /* Light parchment color */
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  padding: 40px;
  margin: 20px;
  max-width: 80%;
  margin: 20px auto;
  text-align: center;
}
.card-title {
  font-size: 3rem;
  font-weight: bold;
  color: #4a3620;
  text-shadow: 2px 2px 8px rgba(255, 255, 255, 0.6);
}
.card-body {
  margin-top: 20px;
  font-size: 1.5rem;
  max-width: 80%;
  line-height: 1.6;
  color: #3a2718;
  text-align: center;
}

/* FOOTER */
.footer {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  background-color: #8b5e34;
}
.footer svg {
  color: #f8f8f8;
  font-size: 1.5rem;
  cursor: pointer;
}
.footer svg:hover {
  color: #ffd700;
}
